#Checkerboard{
    display:flex;
    flex-wrap: wrap;
    align-content: baseline;
    width: var(--board-size);
    height: var(--board-size);
}

.checkerTile{
    position: relative;
    display: block;
    width: calc(100%/var(--num-width));
    height: calc(100%/var(--num-width));
}

#TopTile{
    position: absolute;
    left: 0px;
    width: 110%;
    height: 110%;
}

#BottomTile{
    position: absolute;
    left: 0px;
    width: 110%;
    height: 110%;
}

.topTile{
    position: absolute;
    left: 0;
    top: 0;
}
.bottomTile{
    position: absolute;
    top:0;
    bottom:0;
}